body {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-weight: 500;
}

header {
  text-transform: uppercase;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.nav__list {
  justify-content: space-around;
  margin: 0;
  padding: 0;
  display: flex;
}

.nav__item {
  font-size: 12px;
  line-height: 60px;
  list-style-type: none;
}

.nav__item:not(:first-child) {
  margin-left: 20px;
}

.nav__link {
  color: #060b35;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__link:hover, .is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
}

.container {
  grid-template-columns: 1fr;
  justify-items: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .container {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (width >= 768px) {
  .container {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (width >= 1024px) {
  .container {
    grid-template-columns: repeat(4, 1fr);
  }
}

.card {
  box-sizing: border-box;
  color: #060b35;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 16px;
  transition-duration: .3s;
  display: flex;
}

.card:hover {
  transform: scale(1.2);
}

.card__img {
  width: 160px;
  margin: 16px 3px 0;
}

.card__name {
  margin-top: 40px;
  font-size: 12px;
  line-height: 18px;
}

.card:hover .title {
  color: #34568b;
}

.card__code {
  color: #616070;
  margin-top: 4px;
  font-size: 10px;
  font-weight: 400;
}

.card__raiting {
  justify-content: space-between;
  align-items: center;
  height: 16px;
  margin-top: 16px;
  font-size: 10px;
  font-weight: 400;
  display: flex;
}

.card__price {
  color: #616070;
  justify-content: space-between;
  align-items: center;
  margin-top: 24px;
  font-size: 12px;
  font-weight: 400;
  display: flex;
}

.card__price_value {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
}

.card__btn {
  color: #fff;
  text-transform: uppercase;
  background: #00acdc;
  border: none;
  border-radius: 5px;
  height: 40px;
  margin-top: 16px;
  padding: 0;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 700;
  transition-duration: .3s;
}

.card__btn:hover, .card__btn:active {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.stars {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars--1 :first-child, .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4), .stars--5 :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}
